import ActivitiesData from "../activities.json";
import { logClickEvent } from '../utils/utils';



export const Profile = () => {

  // social_iconsを設定
  const social_icons = [];
  const social_data = [
    {
      "code": "facebook",
      "name": "Facebook",
      "url": "https://www.facebook.com/kazuki.ogiwara.1/"
    }, {
      "code": "twitter",
      "name": "X (Twitter)",
      "url": "https://twitter.com/kaz_ogiwara"
    }, {
      "code": "note",
      "name": "note",
      "url": "https://note.com/kazukio"
    }, {
      "code": "github",
      "name": "GitHub",
      "url": "https://github.com/kaz-ogiwara"
    }, {
      "code": "linkedin",
      "name": "LinkedIn",
      "url": "https://www.linkedin.com/in/和樹-荻原-9ba764122/"
    }
  ];
  for (const i in social_data) {
    const item = social_data[i];
    const html =
      <div className="social-icon" key={item["code"]}>
        <a className={item["code"]} onClick={() => logClickEvent("profile_social", item["code"])}  href={item["url"]} target="_blank"></a>
        <p>{item["name"]}</p>
      </div>
    ;
    social_icons.push(html);
  }

  // activitiesを設定
  const activities = [];
  for (const key in ActivitiesData) {
    const category = ActivitiesData[key];

    const html = <h3 key={category["title"]}>{category["title"]}</h3>;
    activities.push(html);

    for (const i in category["articles"]) {
      const activity = category["articles"][i];
      const imagePath = `/img/${activity["image"]}`; // テンプレートリテラルを使ってパスを作成

      //console.log(activity);
      const html =
        <a href={activity["link"]} target="_blank" key={activity["title"]} className="activity" onClick={() => logClickEvent("profile_activity", activity["title"])}>
          <div className="image">
            <img src={imagePath} alt="" loading="lazy" />
          </div>
          <div className="info">
            <div className="title">{activity["title"]}</div>
            <div className="subtitle">{activity["subtitle"]}</div>
            <div className="media">{activity["media"]}</div>
          </div>
        </a>
      ;

      activities.push(html);
    }
  }

  // 現在年
  const current_date = new Date();
  const current_year = current_date.getFullYear();

  return (
    <div id="component-profile">
      <h3>経歴</h3>
      <div className="profile">Data Graphics Inc. 代表。筑波大学卒業後、2010年東洋経済新報社に入社。2017年英国エディンバラ大学大学院（修士）修了。データ可視化を活用した報道コンテンツの開発、デザイン、記事執筆を行い、グッドデザイン賞などを受賞。スマートニュース メディア研究所、Google News Labを経て2025年より現職。著書に『データ思考入門』（講談社現代新書）など。</div>
      <div className="social-icons">{social_icons}</div>
      <div className="activities">{activities}</div>
      <div className="copyright">© {current_year} Kazuki OGIWARA</div>
    </div>
  );

};
